import {api} from '../../api/api.js'
export default {
	data() {
		return {
			windowWidth:getApp().globalData.windowWidth,
			windowHeight:getApp().globalData.windowHeight,
			top:getApp().globalData.top,
			session_id:getApp().globalData.session_id,
			uid:getApp().globalData.uid,
			usdt:0,
			active:false
		}
	},
	onLoad() {
		this.getInfo();
	},
	methods: {
		back() {
			uni.switchTab({
				url:'../index/index'
			})
		},
		getInfo() {
			uni.request({
				url:api.info,
				data: {
					uid:this.uid,
					session_id:this.session_id
				},
				method:"post",
				header: {
					"Content-Type": "application/x-www-form-urlencoded"
				},
				success: (res) => {
					this.usdt = res.data.result.usdt_money;
				}
			})
		},
		applyNode() {
			this.active = true;
			uni.request({
				url:api.apply_node,
				data: {
					uid:this.uid,
					session_id:this.session_id
				},
				method:"post",
				header: {
					"Content-Type": "application/x-www-form-urlencoded"
				},
				success: (res) => {
					console.log(res.data);
					if(res.data.code === "success") {
						uni.showToast({
							title:res.data.msg,
							icon:"none",
							duration:1500
						})
					}else {
						uni.showToast({
							title:res.data.msg,
							icon:"none",
							duration:1500
						})
					}
					this.active = false;
				}
			})
		}
	}
}
